//----------------------------------------------------
.wrapper-footer{
    text-align: center;
}


//----------------------------------------------------
@media (max-width: 1200px){
    .sidenav-overlay{
        position: fixed;width: 100%;height: 100%;top: 0;left: 0;background-color: #fff;background-color: rgba(225, 225, 225, 0.8);z-index: 5;

        .app-area.collapsed &{
            display: none;
        }
    }
    .wrapper-sidenav{
        position: fixed;width: 90%;height: 100%;top: 0;left: -100%;z-index: 5;transform: translate3d(100%, 0, 0);

        .app-area.collapsed &{
            transform: translate3d(0, 0, 0);
        }
    }
}

@media (min-width: 768px){
    .modal-sm {
        width: 63rem;max-width: 100%;
    }
}

